import React from 'react'

const Counter = ({ id, count, handleINC, handleDEC }) => {
  const onClickINC = () => {
    handleINC(id);
  }

  const onClickDEC = () => {
    handleDEC(id);
  }

  return (
    <div>
      {count} {' '}
      <button onClick={onClickINC}>+</button>
      <button onClick={onClickDEC}>-</button>
    </div>
  );
}

export default Counter;