import { ADD_COUNTER, INCREMENT, DECREMENT } from '../actions/types';

const initialState = {
  counters: [
    { id: 0, count: 0 },
    { id: 1, count: 0 }
  ],
  counterId: 2
}

const countersReducer = (state = initialState, action) => {
  switch (action.type) {
    case INCREMENT:
      return {
        ...state,
        counters: state.counters.map(counter => counter.id === action.id ? { ...counter, count: counter.count + 1 } : counter)
      };
    case DECREMENT:
      return {
        ...state,
        counters: state.counters.map(counter => counter.id === action.id ? { ...counter, count: counter.count - 1 } : counter)
      };
    case ADD_COUNTER:
      let newCounter = {
        id: state.counterId++,
        count: 0
      }
      return {
        ...state,
        counters: [...state.counters, newCounter]
      };
    default:
      return state;
  }
}

export default countersReducer;