import React from 'react';
import { connect } from 'react-redux';
import { addCounter } from '../actions/actionCreators';

const AddCounter = ({ add }) => {
  const onAdd = () => {
    add();
  }

  return (
    <div>
      <button onClick={onAdd}>Add Counter</button>
    </div>
  );
};

const mapDispatch = {
  add: addCounter
}

export default connect(null, mapDispatch)(AddCounter);