import React from 'react';
import Counter from './Counter';
import { connect } from 'react-redux';
import { increment, decrement } from '../actions/actionCreators';


const CounterList = ({ counters,increment, decrement}) => {
  const handleINC = (id) => {
    //console.log("up: " + id);
    increment(id);
  }

  const handleDEC = (id) => {
  //  console.log("down: " + id);
    decrement(id);
  }

  const list = counters.map(counter => (
    <Counter
      key={counter.id}
      id={counter.id}
      count={counter.count}
      handleINC={handleINC}
      handleDEC={handleDEC}
    />
  ));

  return (
    <div>
      {list}
    </div>
  );
}

const mapState = (state) => ({
  counters: state.counter.counters
});

const mapDispatch = {
  increment: increment,
  decrement: decrement
};

export default connect(mapState, mapDispatch)(CounterList);